<template>
  <div class="search">
    <van-nav-bar title="商品搜索" left-arrow @click-left="$router.push('/')" />

    <van-search  v-model="searchName" show-action placeholder="请输入搜索关键词" clearable>
      <template  #action>
        <div @click="search(searchName)">搜索</div>
      </template>
    </van-search>

    <!-- 搜索历史 -->
    <div class="search-history">
      <div class="title">
        <span>最近搜索</span>
        <van-icon @click="clear" name="delete-o" size="16" />
      </div>
      <div class="list">
        <span class="list-item" v-for="(item, index) in history" :key="index" @click="search(item)">{{item}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearchList, setSearchList } from '@/api/search'
export default {
  name: 'SearchIndex',
  data () {
    return {
      searchName: '',
      history: getSearchList()
    }
  },
  methods: {
    search (key) {
      if (key.trim() === '') {
        this.searchName = ''
        return
      }
      const index = this.history.indexOf(key)
      if (index !== -1) {
        this.history.splice(index, 1)
      }
      this.history.unshift(key)
      setSearchList(this.history)
      this.$router.push(`/searchList?search=${key}`)
    },
    clear () {
      this.history = []
      setSearchList([])
    }
  }
}
</script>

<style lang="less" scoped>
* {
  padding: 0;
  margin: 0;
}
.search {
  .searchBtn {
    background-color: #fa2209;
    color: #fff;
  }
  ::v-deep .van-search__action {
    background-color: #c21401;
    color: #fff;
    padding: 0 20px;
    border-radius: 0 5px 5px 0;
    margin-right: 10px;
  }
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .title {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 15px;
  }
  .list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0 10px;
    gap: 5%;
  }
  .list-item {
    text-align: center;
    padding: 3px 10px;
    line-height: 16px;
    border-radius: 12px;
    background: #eee;
    font-size: 10px;
    color: #747474;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-bottom: 10px;
  }
}
</style>
